<template>
  <div id="app">
    <div class="main" v-if="user">
      <div class="backdrop">
        <md-toolbar md-elevation="1">
          <nav class="nav">
            <Logo :faculty="user.faculty" />
            <div class="nav__list">
              <md-button>Пункт</md-button>
              <md-button>Пункт</md-button>
              <md-button>Пункт</md-button>
              <md-button>Пункт</md-button>
              <md-button>Пункт</md-button>
              <md-button @click="logout" class="md-primary">Выйти</md-button>
            </div>
          </nav>
        </md-toolbar>
      </div>
    </div>

    <div class="authorization" v-else>
      <div class="backdrop">
        <div class="container">
          <div class="authorization__header">
            <Logo />
          </div>
          <h1 class="md-display-1 authorization__title">
            Показатели эффективности и стимулирования <br />
            профессорско-педагогического работника
          </h1>
          <div class="authorization__form">
            <h3 class="authorization__form-title md-headline">
              Вход в систему
            </h3>
            <md-field>
              <label>Адрес электронной почты</label>
              <md-input v-model="email" type="email"></md-input>
            </md-field>
            <md-field>
              <label>Пароль</label>
              <md-input v-model="password" type="password"></md-input>
            </md-field>
            <md-button
              @click="signIn"
              class="authorization__form-btn md-raised md-primary"
              >Войти в систему</md-button
            >
            <div class="authorization__form-footer">
              <a href="#" class="authorization__form-forgot md-subheading"
                >Забыли пароль?</a
              >
              <md-checkbox v-model="isRemembered" class="md-primary"
                >Не выходить из системы</md-checkbox
              >
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios"
import Logo from "./components/Logo"

export default {
  name: "App",
  data: () => ({
    token: null,
    isRemembered: true,
    email: "",
    password: "",
    user: null,
  }),
  components: {
    Logo,
  },
  methods: {
    async signIn() {
      const dataForm = {
        email: this.email,
        password: this.password,
      }

      const request = await axios.post(
        "https://sogu-project.herokuapp.com/userLogin",
        dataForm
      )

      const token = await request.data
      this.token = token

      await this.getUser()
    },
    async getUser() {
      const request = await axios.post(
        "https://sogu-project.herokuapp.com/getUser",
        {},
        {
          headers: {
            Authorization: "Bearer " + this.token.token,
          },
        }
      )

      this.user = await request.data.data
    },
    logout() {
      this.token = null
      this.user = null
    },
  },
}
</script>

